.App {
  text-align: center;
}

.centered-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 95vh;
  align-items: center;
}

.btn {
  background-color: #fff;
  border: solid #eee;
  border-width: 0 0 4px 0;
  color: #333;
  padding: 12px 24px;
  font-size: 18px;
  border-radius: 4px;
  display: block;
  transition: all .4s ease;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
}

.btn.btn-rainbow {
  margin: 10px 0;
}

.btn.btn-rainbow:hover {
  background-color: #f83647;
  border-color: #b62534;
  color: #fff;
}

.settings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5px;
}
